@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0 auto;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
  max-width: 760px;
  height: 100%;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #f1f8fe;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.container {
  width: 100%;
  max-width: 360px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 60px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.user-nav {
  display: none;
  justify-content: space-between;
  width: 100%;
  padding: 60px 20px 0 0;
  height: 100%;
}

.user-nav__sub-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  flex-direction: column;
  gap: 20px;
}

.user-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Montserrat", "Arial", sans-serif;
  display: flex;
  gap: 20px;
  padding-top: 8px;
  flex-wrap: wrap;
  position: relative;
  min-width: 230px;
  width: 100%;
  color: #000000;
  flex-direction: column;
}

.user-nav__bottom {
  margin-top: auto;
}

.user-nav__link,
.sub-menu__link {
  font-weight: 500;
  line-height: 20px;
  padding: 5px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.user-nav__link::after,
.sub-menu__link::after {
  content: "";
  display: block;
  width: 10px;
  height: 15px;
  background-image: url("./../img/svg/arrow.svg");
  background-repeat: no-repeat;
}
.user-nav__link:focus,
.sub-menu__link:focus {
  opacity: 0.3;
  order: 2;
}
.user-nav__link:hover,
.sub-menu__link:hover {
  opacity: 0.5;
}

.js-menu {
  padding-left: 35px;
  word-break: break-all;
}
@media (max-width: 767px) {
  .js-menu svg {
    fill: #011c40;
  }
}

.header {
  position: relative;
  background-color: #ffffff;
  height: 100vh;
  width: 100%;
}

.header__box {
  align-items: center;
  width: 100%;
  max-height: 640px;
  overflow-y: scroll;
  margin: 0 auto;
  padding: 0 40px 40px 40px;
  background-color: #ffffff;
}

.header__box-mobile {
  align-self: flex-start;
  margin-top: 12px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 760px;
  position: fixed;
  margin-left: -40px;
  padding: 35px 60px 0 40px;
  background-color: #ffffff;
  z-index: 10;
}

.header__button {
  display: block;
  border: none;
  background-color: transparent;
  width: 24px;
  height: 14px;
  background-image: url("./../img/svg/menu.svg");
  background-repeat: no-repeat;
}
.header__button--close {
  width: 15px;
  height: 14px;
  background-image: url("./../img/svg/close.svg");
  background-repeat: no-repeat;
}

.isOpen {
  display: block;
  animation: fadeInFromNone 1.5s ease-in-out;
  animation-fill-mode: forwards;
}

@keyframes fadeInFromNone {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOutFromBlock {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.isClose {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  opacity: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.animation {
  animation: fadeInOut 1.5s ease-in-out;
}

@keyframes fadeInOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
.lang {
  display: flex;
  flex-direction: column;
}

.lang__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  flex-direction: column;
  position: absolute;
  padding: 20px 0;
  top: 65px;
  width: 180px;
  opacity: 0;
  z-index: 10;
  background-color: #ffffff;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
  animation: fadeOutFromBlock 0.5s ease-in-out;
}

.lang__menu--open {
  display: flex;
  animation: fadeInFromNone 0.5s ease-in-out;
  animation-fill-mode: forwards;
}

@keyframes fadeInFromNone {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOutFromBlock {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.lang__item {
  padding: 5px 20px;
}
.lang__item:hover {
  cursor: pointer;
  background-color: #f1f8fe;
}

.lang__button--child {
  list-style: none;
}

.lang__button {
  width: 80px;
  display: flex;
  gap: 10px;
  align-items: baseline;
  padding-top: 7px;
  height: 35px;
  background: transparent;
  position: relative;
  border: none;
  cursor: pointer;
}
.lang__button::after {
  content: "";
  display: block;
  top: 11px;
  position: absolute;
  right: 0;
  width: 15px;
  height: 15px;
  background-image: url("./../img/svg/arrow-down.svg");
  background-repeat: no-repeat;
}

.sub-menu__title {
  margin: 0;
  padding: 0;
  font-size: 20px;
}

.sub-menu {
  margin-top: 20px;
}

.sub-menu__item {
  padding: 10px 0;
}